.left-sidebar-list{
	padding: 0;
	margin: 0;
	list-style: none;
	@media (max-width: 991px){
		display: none;
	}
	@nest &__item{
		display: block;
		position: relative;
		border-top: 1px solid #ececec;
		@nest & a{
			font-size: 22px;
			color: #4c4c4c;
			display: block;
			padding: 13.5px 22px;
			position: relative;
			text-decoration: none;
			transition: all 300ms;
			@nest &.open{
				@nest &:after{
					transform: rotate(90deg);
				}
			}
			@nest &:after{
				position: absolute;
				content: '';
				right: 22px;
				top: 50%;
				transform: translateY(-50%);
				background: resolve('arrow2.png');
				width: width('arrow2.png');
				height: height('arrow2.png');
			}
			@nest &:before{
				position: absolute;
				content: '';
				left: 0;
				top: 0;
				bottom: 0;
				background: #00d0c4;
				width: 5px;
				height: 100%;
				opacity: 0;
				transition: all 300ms;
			}
			@nest &:hover{
				color: #00d0c4;
				@nest &:before{
					opacity: 1;
				}
			}
			@media (max-width: 1199px){
				font-size: 19px;
			}
		}
	}
}