.header-lang{
	@media (max-width: 767px){
		text-align: center;
	}
	@nest &__desc{
		font-size: 16px;
		color: #fff;
		position: relative;
		padding-left: 30px;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 991px){
			font-size: 14px;
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('lang1.png');
			width: width('lang1.png');
			height: height('lang1.png');
		}
	}
	@nest &__name{
		font-size: 16px;
		color: #fff;
		font-weight: 700;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 991px){
			font-size: 14px;
		}
	}
}