.tovar{
	text-align: center;
	padding: 10px 15px 105px;
	border: 1px solid #ededed;
	margin-bottom: 12px;
	position: relative;
	@media (max-width: 512px){
		max-width: 255px;
		margin: auto;
		margin-bottom: 12px;
	}
	@nest &__bottom{
		position: absolute;
		content: '';
		bottom: 15px;
		left: 0;
		width: 100%;
	}
	@nest &--new{
		position: relative;
		@nest &:after{
			position: absolute;
			content: '';
			right: 0;
			top: 0;
			background: resolve('new.png');
			width: width('new.png');
			height: height('new.png');
		}
	}
	@nest &--sale{
		position: relative;
		@nest &:after{
			position: absolute;
			content: '';
			right: 0;
			top: 0;
			background: resolve('sale.png');
			width: width('sale.png');
			height: height('sale.png');
		}
	}
	@nest &__name{
		font-size: 14px;
		color: #4c4c4c;
	}
	@nest &__price{
		font-size: 16px;
		color: #00c5ba;
		font-weight: 700;
		margin-bottom: 25px;
	}
	@nest &__link{
		font-size: 16px;
		color: #888;
		background: #f5f5f5;
		padding: 0 25px;
		min-height: 40px;
		line-height: 40px;
		text-decoration: none;
		transition: all 300ms;
		display: inline-block;
		vertical-align: top;
		@nest &:hover{
			opacity: .7;
		}
		@media (max-width: 1199px){
			padding: 0 10px;
			font-size: 14px;
		}
	}
	@nest &__backet{
		width: 40px;
		height: 40px;
		background: #ffa200;
		position: relative;
		display: inline-block;
		vertical-align: top;
		transition: all 300ms;
		@nest &:hover{
			background: #00d0c4;
		}
		@nest & img{
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			margin: auto;
		}
	}
}