.footer{
	background: #fff;
	padding-bottom: 20px;
	border-top: 1px solid #ededed;
	@nest &__title{
		font-size: 24px;
		color: #4c4c4c;
		font-weight: 700;
		margin: 35px 0 25px;
		text-transform: uppercase;
		@media (max-width: 512px){
			font-size: 20px;
		}
	}
	@nest &__desc{
		font-size: 14px;
		color: #817f7f;
		line-height: 18px;
	}
}