.nav-list{
	padding: 0;
	margin: 0;
	list-style: none;
	text-align: right;
	@media (max-width: 991px){
		text-align: center;
	}
	@nest &__item{
		display: inline-block;
		padding: 0 10px;
		border-right: 1px solid color(#e4e4e4 alpha(22%));
		@nest &:last-child{
			padding-right: 0;
			border-right: 0;
		}
		@media (max-width: 991px){
			padding: 0 10px;
		}
		@media (max-width: 512px){
			display: block;
			border: 0;
			padding: 0;
			margin: 0 0 3px;
			text-align: center;
		}
		@nest & a{
			font-size: 16px;
			color: #fff;
			text-decoration: none;
			@nest &:hover{
				text-decoration: underline;
			}
			@media (max-width: 991px){
				font-size: 14px;
			}
		}
	}
	@media (max-width: 767px){
		margin: 15px 0 0 0;
	}
}