.footer-nav{
	margin: 35px 0 0 0;
	list-style: none;
	padding: 22px 0 20px;
	border: 1px solid #ebebeb;
	border-right: 0;
	border-left: 0;
	@nest &__item{
		width: 1%;
		display: table-cell;
		@media (max-width: 991px){
			width: auto;
			display: inline-block;
		}
		@nest & a{
			font-size: 14px;
			color: #4c4c4c;
			padding-right: 22px;
			text-decoration: none;
			border-right: 1px solid #dfdfdf;
			@nest &:hover{
				text-decoration: underline;
			}
			@nest &:last-child{
				border-right: 0;
			}
			@media (max-width: 991px){
				border-right: 0;
			}
		}
	}
}