.billboard{
	/* background: resolve('billboard.jpg') no-repeat top center; */
	-webkit-background-size: cover;
	background-size: cover;
	min-height: 460px;
	@media (max-width: 767px){
		text-align: center;
	}
	@nest &__item{
		padding: 130px 0 70px;
		outline: none;
		min-height: 460px;
	}
	@nest &__desc{
		font-size: 48px;
		color: #fff;
		font-weight: 700;
		padding: 5px 20px 5px;
		background: #ffa200;
		text-transform: uppercase;
		margin-bottom: 30px;
		display: inline-block;
		letter-spacing: 0.24px;
		@media (max-width: 767px){
			font-size: 35px;
		}
		@media (max-width: 512px){
			font-size: 30px;
		}
	}
	@nest &__percent{
		font-size: 147px;
		color: #fff;
		font-weight: 700;
		line-height: 125px;
		margin-left: 33px;
		@media (max-width: 991px){
			margin-left: 0;
		}
		@media (max-width: 767px){
			font-size: 100px;
			line-height: normal;
		}
		@media (max-width: 512px){
			font-size: 80px;
		}
	}
}
.slick-prev{
	position: absolute;
	content: '';
	left: 10%;
	top: 50%;
	border: 0;
	z-index: 10;
	outline: none;
	font-size: 0;
	min-width: 25px;
	background-color: transparent;
	transform: translateY(-50%);
	transition: all 300ms;
	@nest &:after{
		position: absolute;
		content: '';
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		background: resolve('slider-left.png');
		width: width('slider-left.png');
		height: height('slider-left.png');
	}
	@nest &:hover{
		opacity: .5;
	}
	@media (max-width: 1199px){
		left: 5%;
	}
	@media (max-width: 991px){
		left: 10px;
	}
}
.slick-next{
	position: absolute;
	content: '';
	right: 10%;
	z-index: 10;
	top: 50%;
	font-size: 0;
	min-width: 25px;
	border: 0;
	outline: none;
	background-color: transparent;
	transform: translateY(-50%);
	transition: all 300ms;
	@nest &:after{
		position: absolute;
		content: '';
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		background: resolve('slider-right.png');
		width: width('slider-right.png');
		height: height('slider-right.png');
	}
	@nest &:hover{
		opacity: .5;
	}
	@media (max-width: 1199px){
		right: 5%;
	}
	@media (max-width: 991px){
		right: 10px;
	}
}