.select{
	position: relative;
	@nest &__current{
		cursor: pointer;
		position: relative;
		padding-right: 17px;
		padding-left: 5px;
		@nest &:hover{
			cursor: pointer;
			text-decoration: underline;
		}
		@nest &:after{
			position: absolute;
			content: '';
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('lang2.png');
			width: width('lang2.png');
			height: height('lang2.png');
		}
		@media (max-width: 767px){
			@nest &:after{
				right: -10px;
			}
		}
	}
	@nest &__list{
		position: absolute;
		content: '';
		left: -90px;
		top: 34px;
		padding: 0;
		margin: 0;
		min-width: 250px;
		z-index: 10;
		list-style: none;
		display: none;
		background: #373636;
		padding: 5px;
		text-align: center;
		@media (max-width: 512px){
			left: -82px;
			min-width: 235px;
		}
	}
	@nest &__item{
		@media (max-width: 767px){
			margin-bottom: 3px;
		}
		@nest &:hover{
			cursor: pointer;
			text-decoration: underline;
		}
	}
}