.steps-block{
	position: relative;
	@nest &:after{
		position: absolute;
		content: '';
		right: 0;
		top: 0;
		width: 1px;
		height: 45px;
		border-right: 1px dotted #000;
		@media (max-width: 767px){
			display: none;
		}
	}
	@nest &--indent{
		padding-left: 45px;
		@nest &:after{
			display: none;
		}
		@media (max-width: 1199px){
			padding-left: 0;
		}
	}
	@media (max-width: 767px){
		text-align: center;
		margin-bottom: 20px;
	}
	@nest &__img{
		max-width: 60px;
		display: inline-block;
		vertical-align: top;
	}
	@nest &__text{
		width: 100%;
		max-width: 195px;
		padding-left: 15px;
		text-align: left;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 1199px){
			max-width: 215px;
		}
		@media (max-width: 991px){
			max-width: 140px;
		}
	}
	@nest &__title{
		font-size: 18px;
		color: #4c4c4c;
		font-weight: 700;
		line-height: 24px;
		margin-bottom: 20px;
		text-transform: uppercase;
		@nest &--border{
			border-right: 0;
		}
		@media (max-width: 1199px){
			border-right: 0;
		}
		@media (max-width: 991px){
			font-size: 14px;
			margin-bottom: 10px;
			line-height: normal;
		}
	}
	@nest &__desc{
		font-size: 13px;
		color: #4c4c4c;
		line-height: 18px;
		@media (max-width: 991px){
			font-size: 12px;
		}
	}
}