.search-select{
	width: 100%;
	max-width: 153px;
	float: left;
	min-height: 50px;
	max-height: 50px;
	background: #f5f5f5;
	border: 1px solid #e6e6e6;
	position: relative;
	text-align: left;
	@media (max-width: 767px){
		float: none;
		vertical-align: top;
	}
	@nest &__list{
		display: none;
		position: absolute;
		content: '';
		left: -50px;
		top: 100%;
		z-index: 10;
		padding: 5px 0;
		margin: 0;
		min-width: 250px;
		list-style: none;
		margin-top: 10px;
		background: #f5f5f5;
	}
	@nest &__current{
		position: absolute;
		content: '';
		top: 50%;
		transform: translateY(-50%);
		max-width: 135px;
		padding-right: 25px;
		overflow: hidden;
		display: inline-block;
		-ms-text-overflow: ellipsis;
		text-overflow: ellipsis;
		@nest &:after{
			position: absolute;
			content: '';
			right: 1px;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('arrow1.png');
			width: width('arrow1.png');
			height: height('arrow1.png');
		}
	}
	@nest &__item{
		margin-bottom: 5px;
	}
	@nest &__item, &__current{
		font-size: 16px;
		color: #8b8b8b;
		font-weight: 700;
		padding-left: 20px;
		@nest &:hover{
			cursor: pointer;
			text-decoration: underline;
		}
		@media (max-width: 767px){
			font-size: 12px;
		}
	}
}