.clother-bilboard{
	min-height: 230px;
	background: resolve('clother.jpg') no-repeat center center;
	-webkit-background-size: cover;
	background-size: cover;
	position: relative;
	@nest &__text{
		position: absolute;
		content: '';
		left: 20px;
		bottom: 20px;
		font-size: 13px;
		color: #fff;
		line-height: 18px;
		margin-bottom: 0;
		letter-spacing: .65px;
	}
}