.footer-list{
	padding: 0;
	margin: 0;
	list-style: none;
	@nest &__item{
		font-size: 14px;
		color: #817f7f;
		line-height: 24px;
		position: relative;
		padding-left: 13px;
		@nest &:after{
			position: absolute;
			content: '';
			width: 3px;
			height: 3px;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			border-radius: 50%;
			background: #ffa200;
		}
		@nest & a{
			font-size: 14px;
			color: #817f7f;
			text-decoration: none;
			@nest &:hover{
				text-decoration: underline;
			}
		}
	}
}