.main-nav__toggle{
	text-align: center;
	padding: 15px 0;
	@nest & span{
		font-size: 16px;
		color: #000;
		margin-right: 10px;
		display: inline-block;
		vertical-align: middle;
	}
	@nest & .btn-menu{
		border: 0;
		outline: none;
		width: 25px;
		height: 13px;
		padding: 0;
		margin: 2px 0 0 0;
		position: relative;
		display: inline-block;
		vertical-align: middle;
		background-color: transparent;
		@nest &__line{
			display: block;
			width: 100%;
			height: 2px;
			background: #000;
			margin-bottom: 3px;
			transition: all 300ms;
		}
	}
	@nest &-open{
		@nest & .btn-menu__line{
			@nest &:first-child{
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				margin: auto;
				transform: rotate(-45deg);
			}
			@nest &:nth-child(2){
				display: none;
			}
			@nest &:nth-child(3){
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				margin: auto;
				transform: rotate(45deg);
			}
		}
	}
	@media (min-width: 767px){
		display: none;
	}
}