.logo{
	margin: 13px 0 0 0;
	display: inline-block;
	transition: all 300ms;
	text-decoration: none;
	@nest &:hover{
		opacity: .7;
	}
	@media (max-width: 991px){
		margin-bottom: 15px;
	}
	@nest &__desc{
		display: inline-block;
		@nest & span{
			font-size: 30px;
			color: #817f7f;
			font-weight: 700;
			padding-top: 5px;
			display: block;
			text-transform: uppercase;
			font-family: var(--cond);
			@nest & i{
				font-size: 16px;
				font-style: normal;
				display: block;
				font-weight: 500;
				margin-top: -4px;
			}
		}
	}
}