@import "normalize-css/normalize.css";
@import "slick-carousel/slick/slick.css";
@import "ionrangeSlider/css/ion.rangeSlider.css";
@import "ionrangeSlider/css/ion.rangeSlider.skinHTML5.css";
@import "fancybox/source/jquery.fancybox.css";
/*! main */
@font-face {
    font-family: 'pf_din_text_cond';
    src: url('../fonts/pfdintextcondpro-medium_2.eot');
    src: url('../fonts/pfdintextcondpro-medium_2.eot?#iefix') format('embedded-opentype'),
         url('../fonts/pfdintextcondpro-medium_2.woff') format('woff'),
         url('../fonts/pfdintextcondpro-medium_2.ttf') format('truetype'),
         url('../fonts/pfdintextcondpro-medium_2.svg#pf_din_text_cond_promedium') format('svg');
    font-weight: 500;
    font-style: normal;
}
@font-face {
    font-family: 'pf_din_text_cond';
    src: url('../fonts/pfdintextcondpro-bold_2.eot');
    src: url('../fonts/pfdintextcondpro-bold_2.eot?#iefix') format('embedded-opentype'),
         url('../fonts/pfdintextcondpro-bold_2.woff') format('woff'),
         url('../fonts/pfdintextcondpro-bold_2.ttf') format('truetype'),
         url('../fonts/pfdintextcondpro-bold_2.svg#pf_din_text_cond_probold') format('svg');
    font-weight: 700;
    font-style: normal;
}

html, body{
	font-family: 'Open Sans', sans-serif;
	font-weight: 400;
}
body{
	@media (max-width: 767px){
		padding: 55px 0 0 0;
	}
}

:root{
	--cond: 'pf_din_text_cond';
}

.block-indent{
	margin-bottom: 15px;
}