.search-form{
	margin: 20px 0 0 0;
	clear: fix;
	@nest & br{
		@media (min-width: 767px){
			display: none;
		}
	}
	@nest &__select{
		display: inline-block;
	}
	@nest &__area{
		width: 100%;
		max-width: 300px;
		float: left;
		min-height: 50px;
		border: 1px solid #e6e6e6;
		border-left: 0;
		border-right: 0;
		padding-left: 20px;
		font-size: 14px;
		color: #8c8c8c;
		outline: none;
		@nest &::placeholder{
			font-size: 14px;
			color: #8c8c8c;
		}
		@media (max-width: 991px){
			max-width: 250px;
		}
		@media (max-width: 767px){
			float: none;
			display: inline-block;
			vertical-align: top;
			border: 1px solid #e6e6e6;
		}
		@media (max-width: 512px){
			margin-top: 10px;
			max-width: 100%;
		}
	}
	@nest &__btn{
		font-size: 18px;
		color: #fff;
		font-weight: 700;
		min-height: 50px;
		border: 0;
		width: 100%;
		outline: none;
		float: left;
		max-width: 132px;
		text-transform: uppercase;
		transition: all 300ms;
		@nest &:hover{
			opacity: .7;
		}
		@media (max-width: 1199px){
			font-size: 14px;
		}
		@media (max-width: 767px){
			float: none;
			vertical-align: top;
			margin-top: 10px;
		}
		@media (max-width: 512px){
			font-size: 13px;
			max-width: 115px;
			min-height: 43px;
		}
		@nest &--search{
			text-align: left;
			padding: 0 0 0 24px;
			background: resolve('btn1.png') no-repeat;
			@media (max-width: 512px){
				background: resolve('btn1.png') no-repeat;
				-webkit-background-size: contain;
				background-size: contain;
			}
		}
		@nest &--backet{
			text-align: right;
			padding: 0 30px 0 0;
			position: relative;
			width: 100%;
			line-height: 50px;
			display: inline-block;
			max-width: 200px;
			margin-left: -18px;
			text-decoration: none;
			background: resolve('btn2.png') no-repeat;
			@nest &:after{
				position: absolute;
				content: '';
				left: 40px;
				top: 50%;
				transform: translateY(-50%);
				background: resolve('backet.png');
				width: width('backet.png');
				height: height('backet.png');
				@media (max-width: 1199px){
					left: 25px;
				}
			}
			@media (max-width: 1199px){
				max-width: 145px;
				padding: 0 15px 0 0;
			}
			@media (max-width: 991px){
				max-width: 165px;
				@nest &:after{
					left: 25px;
				}
			}
			@media (max-width: 512px){
				max-width: 137px;
				line-height: 40px;
			}
		}
	}
}