.filter{
	background: #fff;
	margin: 15px 0;
	border: 1px solid #ececec;
	@nest &__content{
		padding: 17px 15px 20px;
		border-top: 1px solid #ececec;
	}
	@nest &__desc{
		font-size: 14px;
		color: #7b7b7b;
	}
}
.irs{
	top: -18px;
}
.irs-line{
	height: 4px;
	top: 51px;
	background: #fff;
	border: 1px solid #e6e6e6;
}
.irs-bar{
	height: 4px;
	border: 0;
	background: #00d0c4;
}
.irs-bar-edge{
	height: 4px;
	width: 10px;
	border: 0;
	background: #00d0c4;
}
.irs-slider{
	width: 16px;
	height: 16px;
	top: 27px;
	box-shadow: none;
	background: #00d0c4;
	border: 2px solid #fff;
	@nest &:hover{
		background: #00d0c4;
	}
}
.irs-line-mid{
	height: 4px;
}
.irs-from, .irs-to, .irs-single{
	top: 63px;
	padding: 0;
	font-size: 14px;
	color: #7b7b7b;
	font-weight: 700;
	background: transparent;
}
.irs-min, .irs-max{
	top: 63px;
	padding: 0;
	font-size: 14px;
	font-weight: 700;
	color: #7b7b7b;
	background: transparent;
}
.irs-from{
	font-size: 14px;
	color: #00d0c4;
	font-weight: 700;
}