.tovar-card{
	font-size: 0;
	@nest &__col{
		width: 50%;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 767px){
			width: 100%;
			margin-bottom: 10px;
		}
	}
	@nest &__small{
		display: inline-block;
		vertical-align: top;
		@nest & img{
			display: block;
			margin: 0 0 5px 5px;
		}
	}
	@nest &__text{
		font-size: 14px;
		color: #9c9c9c;
		line-height: 18px;
		margin-bottom: 17px;
	}
	@nest &__inp{
		width: 81px;
		height: 45px;
		border: 1px solid #d5d5d5;
		display: inline-block;
		@nest & input{
			width: 47px;
			display: inline-block;
			min-height: 43px;
			border: 0;
			font-size: 18px;
			color: #000;
			vertical-align: top;
			text-align: center;
			@nest &::placeholder{
				font-size: 18px;
				color: #000;
			}
		}
	}
	@nest &__block{
		margin-top: 31px;
	}
	@nest &__btn{
		width: 32px;
		display: inline-block;
		@nest & button{
			width: 32px;
			height: 22px;
			border: 0;
			display: inline-block;
			position: relative;
			background: #eee;
			padding: 0;
			outline: none;
			transition: opacity 300ms;
			@nest &:hover{
				opacity: .7;
			}
			@nest &:after{
				position: absolute;
				content: '';
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
			}
			@nest &#plus{
				background: #00d0c4;
				@nest &:after{
					background: resolve('top.png');
					width: width('top.png');
					height: height('top.png');
				}
			}
			@nest &#minus{
				border: 1px solid #d9d9d9;
				border-top: 0;
				border-right: 0;
				@nest &:after{
					background: resolve('bot.png');
					width: width('bot.png');
					height: height('bot.png');
				}
			}
		}
	}
}
.tovar-image{
	@nest &--lg{
		@nest & img{
			@media (max-width: 512px){
				width: 65%;
			}
		}
	}
}