.pagination{
	margin: 5px 0 0 0;
	@nest &__link{
		width: 42px;
		height: 42px;
		background: #f5f5f5;
		font-size: 18px;
		color: #7b7b7b;
		font-weight: 700;
		border-radius: 2px;
		border: 1px solid #eeeeee;
		line-height: 42px;
		text-decoration: none;
		display: inline-block;
		transition: color 300ms;
		text-align: center;
		margin-right: 5px;
		vertical-align: top;
		@nest &:hover{
			background: #00c5ba;
			color: #fff;
		}
		@nest &--active{
			background: #00c5ba;
			color: #fff;
			border-color: #64bfb9;
		}
		@nest &--next{
			position: relative;
			@nest &:after{
				position: absolute;
				content: '';
				left: 50%;
				top: 50%;
				transition: all 300ms;
				transform: translate(-50%, -50%);
				background: resolve('next.png');
				width: width('next.png');
				height: height('next.png');
			}
			@nest &:hover{
				@nest &:after{
					background: resolve('next-h.png');
					width: width('next-h.png');
					height: height('next-h.png');
				}
			}
		}
	}
}