.top-line{
	margin: 30px 0 35px;
	clear: fix;
	@nest &__left{
		width: 21.5%;
		float: left;
		@media (max-width: 991px){
			width: 100%;
			float: none;
		}
	}
	@nest &__right{
		width: 78.5%;
		float: left;
		@media (max-width: 991px){
			width: 100%;
			float: none;
		}
	}
	@media (max-width: 991px){
		text-align: center;
	}
	@nest &__contacts{
		font-size: 16px;
		color: #6c6262;
		padding: 0 17px 0 25px;
		position: relative;
		margin-right: 15px;
		display: inline-block;
		border-right: 1px solid #e4e4e4;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('cont1.png');
			width: width('cont1.png');
			height: height('cont1.png');
		}
		@nest &--mail{
			padding-left: 30px;
			border-right: 0;
			@nest &:after{
				position: absolute;
				content: '';
				background: resolve('cont2.png');
				width: width('cont2.png');
				height: height('cont2.png');
			}
		}
		@media (max-width: 512px){
			margin: 0 0 5px 0;
			border: 0;
		}
	}
}