.main-nav{
	background: #f5f5f5;
	@media (max-width: 767px){
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		right: 0;
	}
	@nest &-list{
		padding: 0;
		margin: 0;
		list-style: none;
		@nest &__item{
			width: 1%;
			display: table-cell;
			white-space: nowrap;
			text-align: center;
			@media (max-width: 767px){
				width: 100%;
				display: block;
				text-align: center;
			}
			@nest & a{
				font-size: 17px;
				color: #545454;
				font-weight: 700;
				position: relative;
				text-decoration: none;
				display: inline-block;
				text-transform: uppercase;
				transition: all 300ms;
				padding: 15px;
				letter-spacing: 0.085px;
				@nest &:after{
					position: absolute;
					content: '';
					top: 0;
					left: 0;
					right: 0;
					height: 3px;
					width: 100%;
					opacity: 0;
					background: #11d2c7;
					transition: all 300ms;
				}
				@nest &:hover{
					color: #00d0c4;
					@nest &:after{
						opacity: 1;
					}
				}
				@media (max-width: 1199px){
					font-size: 14px;
				}
				@media (max-width: 991px){
					font-size: 13px;
					padding-left: 7px;
					padding-right: 7px;
				}
				@media (max-width: 767px){
					padding: 10px 0;
					font-size: 12px;
				}
			}
		}
	}
}