.tabs{
	@nest &-list{
		padding: 0;
		margin: 25px 0 35px 0;
		list-style: none;
		border-bottom: 1px solid #d0d0d0;
		@nest &__item{
			display: inline-block;
			@nest & button{
				font-size: 14px;
				color: #9c9c9c;
				outline: none;
				min-height: 41px;
				padding: 0 15px;
				font-weight: 700;
				background: #f5f5f5;
				text-transform: uppercase;
				border: 1px solid #d0d0d0;
				border-bottom: 0;
				@media (max-width: 767px){
					font-size: 9px;
				}
			}
			@nest &_active{
				@nest & button{
					padding: 0 50px;
					background: #fff;
					position: relative;
					@media (max-width: 767px){
						padding: 0 15px;
					}
					@nest &:after{
						position: absolute;
						content: '';
						bottom: -1px;
						left: 0;
						right: 0;
						height: 1px;
						background: #fff;
					}
				}
			}
		}
	}
	@nest &__content{
		@nest & p{
			font-size: 14px;
			color: #9c9c9c;
			@media (max-width: 767px){
				font-size: 11px;
			}
		}
	}
}