.content{
	padding: 30px 0;
	background: resolve('content.jpg') no-repeat top center;
	-webkit-background-size: cover;
	background-size: cover;
	border-top: 1px solid #ececec;
	border-bottom: 1px solid #ececec;
	@nest &__title{
		font-size: 24px;
		color: #4c4c4c;
		font-weight: 700;
		min-height: 55px;
		line-height: 55px;
		background: #fff;
		text-transform: uppercase;
		@media (max-width: 1199px){
			font-size: 20px;
		}
		@nest &--indent{
			margin-top: 35px;
		}
		@nest &--collapse{
			@media (max-width: 991px){
				cursor: pointer;
			}
		}
		@nest &--icon{
			position: relative;
			padding-left: 40px;
			margin-left: 18px;
			@nest &:after{
				position: absolute;
				content: '';
				left: 0;
				top: 50%;
				transform: translateY(-50%);
			}
		}
		@nest &--icon1{
			@nest &:after{
				background: resolve('icon1.png');
				width: width('icon1.png');
				height: height('icon1.png');
			}
		}
		@nest &--icon2{
			@nest &:after{
				background: resolve('icon2.png');
				width: width('icon2.png');
				height: height('icon2.png');
			}
		}
		@nest &--icon3{
			@nest &:after{
				background: resolve('icon3.png');
				width: width('icon3.png');
				height: height('icon3.png');
			}
		}
		@nest &--icon4{
			@nest &:after{
				background: resolve('icon4.png');
				width: width('icon4.png');
				height: height('icon4.png');
			}
		}
	}
}