.sale-block{
	max-width: 255px;
	display: block;
	text-decoration: none;
	font-size: 0;
	padding: 20px 17px 17px;
	border-top: 1px solid #ececec;
	@media (max-width: 991px){
		max-width: 235px;
		display: inline-block;
		vertical-align: top;
	}
	@media (max-width: 512px){
		display: block;
		max-width: 100%;
	}
	@nest & img{
		max-width: 100px;
		border: 1px solid #ececec;
		@media (max-width: 1199px){
			width: 40%;
		}
	}
	@nest &__content{
		max-width: 110px;
		padding-left: 10px;
		display: inline-block;
		vertical-align: top;
	}
	@nest &__name{
		font-size: 14px;
		color: #4c4c4c;
		margin: 3px 0 7px;
	}
	@nest &__price{
		font-size: 16px;
		color: #00c5ba;
		font-weight: 700;
	}
}