.add{
	display: block;
	font-size: 18px;
	color: #fff;
	font-weight: 700;
	max-width: 267px;
	padding-left: 47px;
	margin-top: 10px;
	background: #00d0c4;
	position: relative;
	min-height: 45px;
	line-height: 45px;
	text-decoration: none;
	transition: background 300ms;
	@nest &:hover{
		background: #ffa200;
	}
	@nest &:after{
		position: absolute;
		content: '';
		left: 15px;
		top: 50%;
		transform: translateY(-50%);
		background: resolve('star.png');
		width: width('star.png');
		height: height('star.png');
	}
}