.delivery-form{
	padding: 20px 17px 17px;
	border-top: 1px solid #e6e6e6;
	clear: fix;
	@nest &__desc{
		font-size: 14px;
		color: #7b7b7b;
		margin-bottom: 15px;
	}
	@nest &__area{
		width: 80%;
		min-height: 40px;
		padding-left: 15px;
		font-size: 14px;
		color: #ccc;
		float: left;
		outline: none;
		border: 1px solid #e6e6e6;
		border-right: 0;
		@nest &:placeholder{
			font-size: 14px;
			color: #ccc;
		}
	}
	@nest &__btn{
		width: 20%;
		min-height: 40px;
		float: left;
		background: #00d0c4;
		position: relative;
		transition: all 300ms;
		border: 0;
		outline: none;
		@nest &:after{
			position: absolute;
			content: '';
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			background: resolve('arrow3.png');
			width: width('arrow3.png');
			height: height('arrow3.png');
		}
		@nest &:hover{
			background: #ffa200;
		}
	}
}